import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    messages: [],
    unreadCount: 0
  },
  mutations: {
    ADD_MSG(state, message) {
      state.messages.push(message);
    },
    ADD_UNREAD(state) {
      state.unreadCount++;
    },
    RESET_UNREAD(state) {
      state.unreadCount = 0;
    }
  },
  actions: {
    pushMessage(context, message) {
      context.commit('ADD_MSG', message);
    },
    addUnreadMessage(context) {
      context.commit('ADD_UNREAD');
    },
    resetUnreadMessages(context) {
      context.commit('RESET_UNREAD');
    }
  },
  getters: {
    getMessages(state) {
      return state.messages;
    },
    getUnreadCount(state) {
      return state.unreadCount;
    }
  },
  modules: {
  }
})
